<template>
  <v-container fluid pa-0>
    <section class="page">
      <v-parallax dark :src="require('@/assets/img/bg/' + imgsrc)" height="550">
        <v-layout align-center column justify-center>
          <div class="mask-gradient mask-gradient--2"></div>
          <div class="hero-content">
            <v-container>
              <v-flex sm6>
                <h4 class="headline mb-3 text-shadow">{{title}}</h4>
                <span class="body-2 text-shadow">{{content}}</span>
              </v-flex>
            </v-container>
          </div>
        </v-layout>
      </v-parallax>
    </section>

    <!-- TABS AREA -->
    <SubTab class="hidden-sm-and-down">
        <router-link slot="sublink" v-for="(tab, i) in tabs" :key="i" :to="tab.split(' ').join('-')" class="link text-uppercase">{{ tab }}</router-link>
    </SubTab>

    <!-- DETAILS -->
    <ProDetails/>
  </v-container>
</template>

<script>
import ProductLine from "../data/productline.json"
import ProDetails from "../components/_ProDetails"
import SubTab from "../components/_SubTab"

export default {
  name: "ProductDetails",
  components: {
    ProDetails,
    SubTab
  },
  data() {
    return {
      imgsrc: "public_safety.jpg",
      title: "",
      content: "",
      tabs: []
    };
  },
  created() {
    this.getData(this.$route.params.type);
  },
  methods: {
    getData(type) {
      let protype = type.split('-').join(' ');
      for (let i = 0; i < ProductLine.length; i++) {
        if (protype == ProductLine[i].title) {
          this.imgsrc = ProductLine[i].src;
          this.title = ProductLine[i].title;
          this.content = ProductLine[i].content;
          this.tabs = ProductLine[i].tabs;
          return;
        }
      }
    }
  },
  watch: {
    $route(to, from) {
      this.getData(this.$route.params.type);
    }
  }
};
</script>

