<template>
  <v-container fluid pa-0>
    <section class="page">
      <v-parallax dark :src="require('@/assets/img/bg/news.jpg')" height="550">
        <v-layout align-center column justify-center>
          <div class="mask-gradient mask-gradient--2"></div>
          <div class="hero-content">
            <v-container>
              <v-layout>
                <v-flex xs12 sm6>
                  <h4 class="headline text-shadow">Get the latest CSI News and Events</h4>
                </v-flex>
              </v-layout>
            </v-container>
          </div>
        </v-layout>
      </v-parallax>
    </section>

    <!-- NEWS AREA -->
    <v-container id="year">
      <v-layout column>
        <v-flex xs12 class="text-xs-center mt-3">
          <h4 class="display-1 mt-5 mb-4">NEWS & EVENT</h4>
        </v-flex>

        <div class="my-5 text-xs-center tab-btn hidden-sm-and-down">
          <v-btn-toggle v-model="toggle_exclusive" class="no-shadow">
            <v-btn
              v-for="(year, i) in years"
              :key="i"
              depressed
              large
              class="caption px-4 ma-3"
              :to="year+'#year'"
            >{{ year }}</v-btn>
          </v-btn-toggle>
        </div>

        <v-flex xs12 class="hidden-md-and-up">
          <v-select
            v-model="selectedYear"
            :items="years"
            item-value="select"
            background-color="primary"
            dark
            flat
            solo
            single-line
            @change="goToYear(selectedYear)"
          ></v-select>
        </v-flex>

        <v-flex xs12 mb-5>
          <v-dialog v-model="dialog" lazy full-width scrollable no-click-animation transition="slide-x-transition">
            <template slot="activator">
              <v-container pa-0>
                <v-layout row wrap class="border-center">
                  <v-flex
                    xs12 sm6
                    :class="[{'pa-4':$vuetify.breakpoint.smAndUp}, 'd-flex', 'xs6']"
                    v-for="article in articles.slice().reverse()"
                    :key="article.id"
                    @click="openNews(article.id)"
                  >
                      <v-card flat class="news-card pa-3">
                        <v-layout>
                          <div class="news-date text-md-center py-4 pr-4">
                            <h2 class="display-2 news-date__day">{{ article.date | moment("DD") }}</h2>
                            <div
                              class="news-date__month font-weight-light text-uppercase"
                            >{{ article.date | moment("MMMM") }}</div>
                          </div>
                          <v-flex>
                              <div>
                                <div class="headline my-3">{{article.title}}</div>
                                <div v-if="article.video" class="mb-3">
                                  <youtube :video-id="article.video" ref="youtube" width="100%" height="225"></youtube>
                                </div>
                                <div v-else-if="article.src">
                                  <v-img :src="'../img/news/' + article.src" width="100%" height="220px" cover position="center 10%"></v-img>
                                </div>
                                <div v-else>
                                  <span v-html="$options.filters.getBrief(article.body, 200)" class="text-opacity"></span>
                                  <!-- {{ article.body | getBrief(200)}} -->
                                  <p class="mt-3">
                                    <span
                                      class="text-capitalize primary--text font-weight-medium"
                                      to="productdetails"
                                    >Read more ></span>
                                  </p>
                                </div>
                              </div>
                          </v-flex>
                        </v-layout>
                      </v-card>
                  </v-flex>
                </v-layout>
              </v-container>
            </template>

            <v-card :class="$vuetify.breakpoint.smAndDown ? 'pa-3' : 'pa-5'">
              <v-layout row overflow-hidden>
                <v-flex shrink align-self-center class="hidden-sm-and-down">
                  <v-btn fab dark depressed color="primary" class="mr-3">
                    <img
                      :src="require('@/assets/icon/back.svg')"
                      alt="Back"
                      @click="closeDialog"
                    >
                  </v-btn>
                </v-flex>

                <v-divider class="mx-5 hidden-sm-and-down" inset vertical></v-divider>

                <v-flex>
                  <v-layout row overflow-hidden fill-height lazy :class="{'wrap':$vuetify.breakpoint.xsOnly}">
                    <v-flex :class="$vuetify.breakpoint.smAndUp ? 'shrink' : 'xs12'">
                      <div class="news-date text-md-center pr-5 mb-4">
                        <h2 class="display-2 news-date__day">{{ dialogDate | moment("DD") }}</h2>
                        <div
                          class="news-date__month font-weight-light text-uppercase"
                        >{{ dialogDate | moment("MMMM") }}</div>
                      </div>
                    </v-flex>
                    <v-flex overflow-x-hidden>
                      <v-layout row wrap :class="$vuetify.breakpoint.xsOnly ? 'height-60' : 'fill-height'">
                        <v-flex xs12>
                          <div class="headline pa-0 mb-4">{{dialogTitle}}</div>
                        </v-flex>
                        <v-flex v-if="dialogVideo" xs12 md5 class="pr-4 mb-3">
                          <youtube :video-id="dialogVideo" ref="youtube" resize :width="$vuetify.breakpoint.smAndDown ? '250' : '640'" :height="$vuetify.breakpoint.smAndDown ? '150' : '320'"></youtube>
                        </v-flex>
                        <v-flex v-else-if="dialogSrc" xs12 md5 class="pr-4">
                          <img :src="'../img/news/' + dialogSrc" width="100%"/>
                        </v-flex>
                        <v-flex :class="[{ 'md7': dialogSrc}, { 'md12': dialogVideo}, 'xs12']">
                          <div v-html="dialogBody" class="text-opacity"></div>
                        </v-flex>
                      </v-layout>
                    </v-flex>
                  </v-layout>
                </v-flex>
              </v-layout>

              <v-card-actions class="mt-4">
                <v-spacer></v-spacer>
                <social-sharing :url="dialogURL"
                                :title="dialogTitle"
                                :description="dialogBody"
                                hashtags="CSI Technology Group, InfoShare"
                                inline-template>
                  <div>
                      <network network="facebook">
                        <v-btn fab depressed small color="grey lighten-4" class="ml-3">
                          <img :src="require('@/assets/icon/facebook.svg')" class="opacity-3"/>
                        </v-btn>
                      </network>
                      <network network="twitter">
                        <v-btn fab depressed small color="grey lighten-4" class="ml-3">
                          <img :src="require('@/assets/icon/twitter.svg')" class="opacity-3" />
                        </v-btn>
                      </network>
                      <!-- <network network="linkedin">
                        <v-btn fab depressed small color="grey lighten-4" class="ml-3">
                          <img :src="require('@/assets/icon/linkedin.svg')" class="opacity-3"/>
                        </v-btn>
                      </network> -->
                  </div>
                </social-sharing>
              </v-card-actions>

            </v-card>
          </v-dialog>
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
import news2014 from "../data/news/news-2014.json";
import news2015 from "../data/news/news-2015.json";
import news2016 from "../data/news/news-2016.json";
import news2017 from "../data/news/news-2017.json";
import news2018 from "../data/news/news-2018.json";
import news2019 from "../data/news/news-2019.json";

export default {
  name: "News",
  data: () => ({
    dialog: false,
    selectedYear: '',
    years: ["2019", "2018", "2017", "2016", "2015", "2014"],
    viewAll: true,
    isActive: false,
    toggle_exclusive: 0,
    articles: "",
    selectedArticle: "",
    dialogID: "",
    dialogTitle: "",
    dialogDate: "",
    dialogSrc: "",
    dialogBody: "",
    dialogVideo: "",
    dialogURL: "",
    select: "2019"
  }),
  mounted() {
    let year = this.$route.params.year;
    let eventid = this.$route.query.id;

    this.selectedYear = year;
    this.switchYear(year);

    if(eventid){
      this.openNews(eventid);
    }
  },
  methods: {
    switchYear(year) {
      this.selectedYear = year;
      switch (year) {
        case "2014":
          this.articles = news2014;
          this.toggle_exclusive = 5;
          break;
        case "2015":
          this.articles = news2015;
          this.toggle_exclusive = 4;
          break;
        case "2016":
          this.articles = news2016;
          this.toggle_exclusive = 3;
          break;
        case "2017":
          this.articles = news2017;
          this.toggle_exclusive = 2;
          break;
        case "2018":
          this.articles = news2018;
          this.toggle_exclusive = 1;
          break;
        default:
          this.articles = news2019;
          this.toggle_exclusive = 0;
      }
    },
    goToYear(val) {
        this.$router.push({path:'' + val + '#year' });
    },
    openNews(eventid){
      for (let i=0; i<this.articles.length; i++) {
        if(eventid == this.articles[i].id) {
          this.selectedArticle = this.articles[i];
          this.getNewsData(this.selectedArticle);
          return
        }
      }
    },
    getNewsData(article) {
      this.dialog = true;
      this.dialogID = article.id;
      this.dialogTitle = article.title;
      this.dialogDate = article.date;
      this.dialogSrc = article.src;
      this.dialogBody = article.body;
      this.dialogVideo = article.video;
      this.dialogURL = window.location.href.split('?')[0] + "?id=" + this.dialogID;
    },
    resetData() {
      this.dialogID = ''
      this.dialogTitle = ''
      this.dialogDate = ''
      this.dialogSrc = ''
      this.dialogBody = ''
      this.dialogVideo = ''
      this.dialogURL = ''
    },
    closeDialog() {
      this.dialog = false;
      this.resetData();

      if (this.$route.query.id) {
        this.$router.replace({
          name: 'news',
          query: ''
        })
      }
    },
    stop() {
      if(this.$refs.youtube){
        this.$refs.youtube.player.stopVideo();
      }
    }
  },
  watch: {
    $route(to, from) {
      this.switchYear(this.$route.params.year);
      this.openNews(this.$route.query.id);
    },
    dialog(val) {
      !val && this.stop();
      !val && this.closeDialog();
      //document.querySelector('html').classList.toggle('application--dialog-opened', val);
    }
  }
};
</script>

<style lang="scss">
.application--dialog-opened{
  overflow: hidden;
}
</style>
