<template>
  <v-container fluid pa-0>
    <section>
      <v-container>
        <v-layout row wrap align-center justify-center class="p4-5">
          <v-flex sm10 class="text-sm-center">
            <h4 class="display-1 text-gradient mt-5 mb-4">{{name}}</h4>
            <h5 class="body-2 primary--text mb-2">{{title}}</h5>
            <p class="body-1 font-weight-light" v-html="subtitle"></p>
          </v-flex>
          <v-flex xs12 text-sm-center>
            <v-btn
              v-show="site"
              depressed
              round
              color="accent"
              :href="site"
              target="_blank"
              class="my-4 px-4"
            >Explore Site</v-btn>

            <v-dialog v-model="dialog" width="640">
              <template slot="activator">
                <v-btn v-show="video" depressed round color="accent" class="my-4 px-5">Video</v-btn>
              </template>

              <v-card class="line-height-none">
                  <youtube :video-id="video" ref="youtube" @playing=""></youtube>
              </v-card>
            </v-dialog>

          </v-flex>
        </v-layout>
      </v-container>
    </section>

    <section v-if="highlight" class="color-block mb-5">
      <v-container py-0>
        <v-layout align-center justify-center row wrap>
          <v-flex sm12 md6>
            <v-card flat color="transparent" :class="$vuetify.breakpoint.xsOnly ? 'mx-2 px-4' : 'mx-5 px-5'">
              <div class="product-img img-shadow">
                <v-img :src="require('../assets/img/product/'+ thumbsrc)" height="250"></v-img>
              </div>

              <v-list v-if="brochure" class="transparent mt-3">
                <v-list-tile @click.prevent="downloadFile(brochure)">
                  <v-list-tile-action>
                    <img src="@/assets/icon/icon-pdf.svg" alt="PDF">
                  </v-list-tile-action>

                  <v-list-tile-content>
                    <v-list-tile-title class="font-weight-light opacity-2">Brochure</v-list-tile-title>
                  </v-list-tile-content>

                  <v-list-tile-action>
                    <img src="@/assets/icon/icon-download.svg" alt="PDF">
                  </v-list-tile-action>
                </v-list-tile>
              </v-list>
            </v-card>
          </v-flex>
          <v-flex sm12 md6 :class="[$vuetify.breakpoint.xsOnly ? 'pl-0' : 'pl-5', 'mt-5']">
            <h4 class="headline mb-4">Highlights</h4>
            <ul class="highlight">
              <li
                class="body-1 font-weight-light opacity-2"
                v-for="(item, i) in highlight"
                :key="i"
              >{{item}}</li>
            </ul>
          </v-flex>
        </v-layout>
      </v-container>
    </section>

    <br>

    <section v-if="features" class="features-bg mt-5 pb-4">
      <v-container grid-list-xl>
        <div class="arrow-deco"></div>
        <h4 class="headline my-5">Features</h4>
        <v-layout row wrap>
          <v-flex sm4 v-for="(feature, j) in features" :key="j" class="mb-4">
            <v-layout>
              <v-flex shrink>
                <div class="bg-gradient-top numeric-box mr-1">
                  <div class="white--text headline font-weight-medium">{{ j+1 }}</div>
                </div>
              </v-flex>
              <v-flex class="pr-4">
                <span class="body-1 font-weight-light opacity-1">{{ feature }}</span>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-container>
    </section>

    <section v-if="content" class="my-5">
      <v-container>
        <div class="mx-auto my-5">
          <v-img :src="require('@/assets/img/product/'+content)" max-width="100%" position="center" contain></v-img>
        </div>
      </v-container>
    </section>

    <section v-if="contentText" class="my-5">
      <v-container>
        <div class="body-1 text-sm-center opacity-2 px-5" v-html="contentText"></div>
      </v-container>
    </section>
  </v-container>
</template>

<script>
import axios from "axios";
import Products from "../data/products.json";

export default {
  name: "ProDetails",
  data() {
    return {
      dialog: false,
      playerVars: {
        autoplay: 1
      },
      name: "",
      title: "",
      subtitle: "",
      brochure: "",
      thumbsrc: "",
      highlight: "",
      features: "",
      content: "",
      contentText: "",
      site: "",
      video: ""
    };
  },
  created() {
    this.getData(this.$route.params.id);
  },
  methods: {
    getData(id) {
      let proid = id.split("-").join(" ");
      for (let i = 0; i < Products.length; i++) {
        if (proid == Products[i].name) {
          this.name = Products[i].name;
          this.title = Products[i].title;
          this.subtitle = Products[i].subtitle;
          this.brochure = Products[i].brochure;
          this.thumbsrc = Products[i].thumbsrc;
          this.highlight = Products[i].highlight;
          this.features = Products[i].features;
          this.content = Products[i].content;
          this.contentText = Products[i].contentText;
          this.site = Products[i].site;
          this.video = Products[i].video;
          return;
        }
      }
    },
    downloadFile(file) {
      axios({
        url: "../../../brochure/" + file,
        method: "GET",
        responseType: "arraybuffer"
      })
        .then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', file );
          document.body.appendChild(link);
          link.click();
        })
        .catch(() => console.log("error occured"));
    },
    stop() {
      this.$refs.youtube.player.stopVideo()
    }
  },
  watch: {
    $route(to, from) {
      this.getData(this.$route.params.id);
    },
    dialog(val) {
      !val && this.stop();
    }
  }
};
</script>

