<template>
    <v-dialog
      v-model="show"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-toolbar flat color="white" class="fluid">
          <v-btn flat icon @click="closeDialog">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>

        <div class="overflow-x-hidden">
          <div>
            <v-img :src="require('@/assets/img/bg/contact.jpg')" height="300"></v-img>
          </div>

          <v-container>
            <v-container mt-5 class="contact-bg">
              <v-layout row wrap>
                <v-flex sm12>
                  <h3 class="headline px-3">Get In Touch With Us</h3>
                </v-flex>
                <v-flex sm5>
                  <div
                    class="body-2 font-weight-light opacity-2 my-4 px-3"
                  >We love hearing from you. If you have any question or would like to schedule a demo, please fill out this form. We will get back to you right away.</div>

                  <v-list>
                    <v-list-tile v-for="(info, i) in Infos" :key="i" class="mb-2">
                      <v-list-tile-action class="pr-3">
                        <img :src="require('@/assets/icon/'+info.icon)">
                      </v-list-tile-action>

                      <v-list-tile-content>
                        <v-list-tile-title
                          v-text="info.content"
                          class="body-1 font-weight-light opacity-2"
                        ></v-list-tile-title>
                      </v-list-tile-content>
                    </v-list-tile>

                    <v-list-tile class="mt-4">
                      <v-list-tile-action class="pr-3">
                        <div class="primary-line"></div>
                      </v-list-tile-action>
                      <v-list-tile-content>
                        <a href="https://goo.gl/maps/ahY1Qez8rRG2" target="_blank">View on map</a>
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list>
                </v-flex>

                <v-flex sm7>
                  <v-container mb-5>
                    <v-form ref="form" @submit="validate" action="" method="post" enctype="text/plain" role="form">
                      <v-layout row wrap>
                        <v-flex xs3 sm4 class="text-sm-right">
                          <div class="body-1 font-weight-light opacity-2 mt-2 mr-3">Name</div>
                        </v-flex>
                        <v-flex xs9 sm8>
                          <v-text-field
                            v-model="name"
                            name="name"
                            :rules="[rules.required]"
                            flat
                            single-line
                            solo
                            small
                            background-color="grey lighten-3"
                            required
                          ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm4 class="text-sm-right">
                          <div class="body-1 font-weight-light opacity-2 mt-2 mr-3">Email</div>
                        </v-flex>
                        <v-flex xs9 sm8>
                          <v-text-field
                            v-model="email"
                            name="email"
                            :rules="[rules.required, rules.email]"
                            flat
                            single-line
                            solo
                            background-color="grey lighten-3"
                            required
                          ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm4 class="text-sm-right">
                          <div class="body-1 font-weight-light opacity-2 mt-2 mr-3">Phone</div>
                        </v-flex>
                        <v-flex xs9 sm8>
                          <v-text-field
                            v-model="phone"
                            name="phone"
                            :rules="[rules.phone]"
                            mask="(###)###-####"
                            flat
                            single-line
                            solo
                            background-color="grey lighten-3"
                            required
                          ></v-text-field>
                        </v-flex>
                        <v-flex xs3 sm4 class="text-sm-right">
                          <div class="body-1 font-weight-light opacity-2 mt-2 mr-3">Message</div>
                        </v-flex>
                        <v-flex xs9 sm8>
                          <v-textarea
                            v-model="message"
                            name="message"
                            rows="10"
                            no-resize
                            flat
                            solo
                            background-color="grey lighten-3"
                            required
                          ></v-textarea>
                        </v-flex>
                        <v-flex xs12 class="text-sm-right">
                          <v-btn
                            depressed
                            color="primary"
                            class="mx-0 px-5"
                            type="button"
                            @click="validate"
                          >Send</v-btn>
                        </v-flex>
                      </v-layout>
                    </v-form>
                  </v-container>
                </v-flex>
              </v-layout>
            </v-container>
          </v-container>
        </div>
      </v-card>
    </v-dialog>
</template>

<script>
export default {
  name: "ContactForm",
  $_veeValidate: {
      validator: 'new'
  },
  props: {
    value: Boolean
  },
  data: () => ({
    Infos: [
      { icon: "tel.svg", content: "+(732) 346-0200" },
      { icon: "fax.svg", content: "+(732) 346-0209" },
      { icon: "mail.svg", content: "sales@csitech.com" },
      { icon: "location.svg", content: "330 Mac Lane Keasbey, NJ 08832, U.S.A" }
    ],
    name: "",
    email: "",
    phone: "",
    message: "",
    rules: {
      required: v => !!v || "Required",
      email: v => /.+@.+/.test(v) || "E-mail must be valid",
      phone: v => v.length >= 10 || "Phone must be valid"
    }
  }),
  computed: {
    show: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('input', value)
      }
    }
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.$validator.validateAll();
        this.sendMail();
      } else {
        event.preventDefault();
      }
    },
    reset() {
      this.$refs.form.reset()
    },
    closeDialog() {
      this.show = false;
      this.reset();
    },
    sendMail() {
      let url = `mailto:sales@csitech.com?subject=Contact CSI&body=${this.message}%0d%0a--------------------------------------------%0d%0a${this.name}%0d%0a Phone: ${this.phone}%0d%0a Email: ${this.email}`;
      window.location = url;
    }
  }
};
</script>
