<template>
  <v-container my-5>

    <v-layout column>
      <v-flex xs12 class="text-xs-center mb-5">
        <h4 class="display-1 mt-5 mb-4">Our Team</h4>
        <p class="body-1 font-weight-light">CSIs technical and client services teams are working together to provide existing and future clients with world-class applications</p>
      </v-flex>

      <div class="my-5 text-xs-center tab-btn hidden-xs-only">
        <v-btn-toggle v-model="toggle_exclusive" class="no-shadow">
          <v-btn v-for="(team, i) in teams" :key="i" depressed large class="caption px-4 ma-3" @click="switchType(team)">{{ team }}</v-btn>
        </v-btn-toggle>
      </div>

      <v-flex xs12 class="hidden-sm-and-up">
          <v-select
            v-model="teamGroup"
            :items="teams"
            item-value="select"
            background-color="primary"
            dark
            flat
            solo
            single-line
            @change="switchType(teamGroup)"
          ></v-select>
        </v-flex>

      <v-flex xs12>
        <v-layout row wrap>
          <v-flex :class="[$vuetify.breakpoint.xsOnly ? 'pa-0' : 'pa-4', 'flex-card xs6']"
          v-for="staff in staffs.slice(0, -1)"
          v-if="staff.type == teamGroup || teamGroup == 'All'" :key="staff.id">
            <v-card class="staff-card hover" height="100%">
              <v-layout row wrap text-xs-center text-sm-left>
                <v-flex xs12 sm5 md4 align-center justify-center d-flex>
                  <v-avatar
                    :size="avatarSize"
                  >
                    <img :src="require('@/assets/img/leadship/' + staff.src)" class="mx-4 pa-2"/>
                  </v-avatar>
                </v-flex>

                <v-flex xs12 sm7 md8 py-4 :class="$vuetify.breakpoint.xsOnly ? '' : 'pr-3'">
                  <div class="body-2 primary--text">{{ staff.name }}</div>
                  <div class="caption mb-2">{{ staff.position }}</div>
                  <div class="caption font-weight-light opacity-2 hidden-sm-and-down">{{ staff.desc | getBrief(200) }}</div>
                </v-flex>
              </v-layout>

              <div class="staff-card-detail">
                <v-card dark tile flat class="pa-4 bg-gradient-right">
                  <v-list-tile-title class="body-2">{{ staff.name }}</v-list-tile-title>
                  <div class="caption mb-2">{{ staff.position }}</div>
                  <span class="caption font-weight-light staff-card-content">{{ staff.desc }}</span>
                </v-card>
              </div>
            </v-card>
          </v-flex>

          <v-flex :class="[$vuetify.breakpoint.xsOnly ? 'pa-0' : 'pa-4', 'xs12']" v-for="staff in staffs"
          v-if="staff.type == 'Advisor' && teamGroup == 'All'" :key="staff.id">
            <div class="headline text-xs-center my-5">Special Thanks to Our Advisor</div>
            <v-card class="px-4 staff-card">
              <v-layout row wrap text-xs-center text-sm-left>
                <v-flex xs12 sm2 md2 align-center justify-center d-flex>
                  <v-avatar
                    :size="avatarSize"
                  >
                    <img :src="require('@/assets/img/leadship/' + staff.src)" class="mr-4 pa-2"/>
                  </v-avatar>
                </v-flex>

                <v-flex xs12 sm10 md10 py-4>
                  <div class="body-2 primary--text">{{ staff.name }}</div>
                  <div class="caption mb-2">{{ staff.position }}</div>
                  <div class="caption font-weight-light opacity-2">{{ staff.desc }}</div>
                </v-flex>
              </v-layout>
            </v-card>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import Staffs from "../data/leadership.json";

  export default {
    name: 'Overall',
    data() {
      return {
        teams: ['All', 'Director', 'Sales', 'Account Management'],
        toggle_exclusive: 0,
        staffs: Staffs,
        teamGroup: 'All',
        avatarSize: '150',
      }
    },
    computed: {
      avatarSize() {
        switch (this.$vuetify.breakpoint.name) {
          case 'xs': return 44
          case 'sm': return 88
          case 'md': return 100
          case 'lg': return 130
          case 'xl': return 130
        }
      }
    },
    methods: {
      switchType(type) {
        this.teamGroup = type;
        switch (type) {
          case "Account Management":
            this.toggle_exclusive = 3;
            break;
          case "Sales":
            this.toggle_exclusive = 2;
            break;
          case "Director":
            this.toggle_exclusive = 1;
            break;
          default:
            this.toggle_exclusive = 0;
        }
      }
    },
    mounted () {
      console.log(this.$vuetify.breakpoint.mdAndDown);
      if(this.$vuetify.breakpoint.mdAndDown) {
        this.avatarSize = '100';
      }
    }
  }
</script>
