    <div class="flex pt-2 bg-gradient-right">
      <div class="footer secondary">
        <v-container>
          <img src="@/assets/csi-logo-company.png" alt="CSI Technology Group" class="my-4">

          <v-card flat tile class="mt-5" color="transparent">
            <v-layout flex align-start pa-0 row wrap>
              <v-flex sm12 md9 class="hidden-sm-and-down">
                <div class="footer-list">
                  <div class="footer-list-tile mb-4" v-for="(data, i) in hasTabDatas" :key="i">
                    <div class="">
                      <p class="body-1">{{ data.title }}</p>
                      <div class="list">
                        <router-link v-for="(tab, j) in data.tabs" :key="j"
                        :to="{
                          name: 'productdetails',
                          params: { type: data.title.split(' ').join('-'), id: tab.split(' ').join('-') }
                        }"
                        class="footer-link"
                        >{{ tab }}</router-link>
                      </div>
                    </div>
