                  <div class="footer-list-tile mb-4">
                    <div class="">
                      <p class="body-1">Others</p>
                      <div class="list" v-for="(data, index) in noTabDatas" :key="index">
                        <a v-if="data.href"
                        :href="'/' + data.href"
                        target="_blank"
                        class="footer-link"
                        >{{ data.title }}</a>
                        <router-link v-else
                        :to="{
                          name: 'productdetails',
                          params: { type: data.title.split(' ').join('-'), id: data.title.split(' ').join('-') }
                        }"
                        class="footer-link"
                        >{{ data.title }}</router-link>
                      </div>
                    </div>
                  </div>
                </div>
              </v-flex>

              <!-- Expansion Panel -->
              <v-flex class="mb-5 hidden-md-and-up">
                <v-expansion-panel focusable flat class="no-shadow">
                  <v-expansion-panel-content v-for="(data, i) in hasTabDatas" :key="i" class="expansion-panel">
                    <template slot="header" class="px-0">
                      <div>{{ data.title }}</div>
                    </template>
                    <v-card flat color="transparent">
                      <v-card-text>
                        <router-link v-for="(tab, j) in data.tabs" :key="j"
                        :to="{
                          name: 'productdetails',
                          params: { type: data.title.split(' ').join('-'), id: tab.split(' ').join('-') }
                        }"
                        class="footer-link"
                        >{{ tab }}</router-link>
                      </v-card-text>
                    </v-card>
                  </v-expansion-panel-content>

                  <v-expansion-panel-content class="expansion-panel">
                    <template slot="header" class="px-0">
                      <div>Others</div>
                    </template>
                    <v-card flat color="transparent">
                      <v-card-text>
                        <div class="list" v-for="(data, index) in noTabDatas" :key="index">
                        <a v-if="data.href"
                        :href="'/' + data.href"
                        target="_blank"
                        class="footer-link"
                        >{{ data.title }}</a>
                        <router-link v-else
                        :to="{
                          name: 'productdetails',
                          params: { type: data.title.split(' ').join('-'), id: data.title.split(' ').join('-') }
                        }"
                        class="footer-link"
                        >{{ data.title }}</router-link>
                      </div>
                      </v-card-text>
                    </v-card>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-flex>

              <!-- NEWS AREA -->
              <v-flex sm12 md3>
                <div class="news-box">
                  <p class="caption font-weight-medium opacity-3">Recent Events</p>
                  <v-layout>
                    <v-flex xs12>
                      <router-link :to="'/news/2019?id=' + id">
                        <v-card flat color="transparent">
                          <v-img
                            :src="'../img/news/' + src"
                            class="my-2 mask-gradient"
                            contain
                            aspect-ratio="1.7778"
                            height="165px"
                          ></v-img>
                          <v-card-title primary-title class="pa-0">
                            <div>
                              <div class="caption small font-weight-regular my-2">{{ article }}</div>
                              <div class="caption small opacity-3">{{ body | getBrief(120) }}
                              </div>
