                  >Copyright  CSI Technology Group. All rights reserved.</div>
                </v-flex>
                <v-flex xs12 sm6 :class="$vuetify.breakpoint.smAndUp ? 'text-xs-right' : ''">
                  <router-link
                    v-for="(term, i) in terms"
                    :key="i"
                    tag="a"
                    :to="term.href"
                    class="link dark opacity-2"
                  >{{ term.name }}</router-link>
                </v-flex>
              </v-layout>
