<script>
import ProductLine from "../data/productline.json";
import news2019 from "../data/news/news-2019.json";
import Jobs from "../components/_Jobs"

export default {
  name: "Footer",
  components: {
      Jobs
  },
  data: () => ({
    datas: ProductLine,
    id: '',
    article: '',
    src: '',
    body: '',
    terms: [
      {
        name: "Terms of Service",
        href: "/terms"
      },
      {
        name: "Privacy Policy",
        href: "/terms/#PrivacyPolicy"
      }
    ]
  }),
  computed: {
    hasTabDatas() {
      return this.datas.filter(data => data.tabs && data.tabs.length > 2)
    },
    noTabDatas() {
      return this.datas.filter(data => !data.tabs || data.tabs.length < 2)
    },
  },
  created() {
    this.getNewEvent();
  },
  methods: {
    getNewEvent() {
      let news = news2019.slice().reverse();
      for (let i=0; i < news.length; i++) {
        if(news[i].type == 'event'){
          this.id = news[i].id;
          this.article = news[i].title;
          this.src = news[i].src;
          this.body = news[i].body;
          return
        }
      }
    }
  },
};
</script>

