<template>
  <div>
    <v-toolbar
      app
      flat
      absolute
      color="transparent"
      :height="$vuetify.breakpoint.mdAndDown ? '80' : '100'"
      :class="[{dark: isActive}, 'main-toolbar']"
    >
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat :ripple="false" :to="items[0].path" target="_self" class="pa-0 line">
          <span>{{ items[0].title }}</span>
        </v-btn>
      </v-toolbar-items>

      <v-toolbar-items class="hidden-sm-and-down">
        <v-menu
          offset-y
          open-on-hover
          dark
          full-width
          min-width="100%"
          content-class="position-left-0"
        >
          <template slot="activator">
            <v-btn
              flat
              :ripple="false"
              :to="items[1].path"
              target="_self"
              class="pa-0 line"
            >{{ items[1].title }}</v-btn>
          </template>
          <v-list class="primary">
            <v-card flat tile class="px-5 pt-4" color="transparent">
              <v-layout flex align-start pa-0 justify-center>
                <v-flex class="text-sm-center">
                  <div class="footer-list text-sm-left">
                    <div class="footer-list-tile mb-4" v-for="(data, i) in hasTabDatas" :key="i">
                      <div class>
                        <p class="body-1">{{ data.title }}</p>
                        <div class="list">
                          <router-link
                            v-for="(tab, j) in data.tabs"
                            :key="j"
                            :to="{
                          name: 'productdetails',
                          params: { type: data.title.split(' ').join('-'), id: tab.split(' ').join('-') }
                        }"
                            class="footer-link-product"
                          >{{ tab }}</router-link>
                        </div>
                      </div>
                    </div>

                    <div class="footer-list-tile mb-4">
                      <div class>
                        <p class="body-1">Others</p>
                        <div class="list" v-for="(data, index) in noTabDatas" :key="index">
                          <a
                            v-if="data.href"
                            :href="'/' + data.href"
                            target="_blank"
                            class="footer-link-product"
                            replace
                          >{{ data.title }}</a>
                          <router-link
                            v-else
                            :to="{
                          name: 'productdetails',
                          params: { type: data.title.split(' ').join('-'), id: data.title.split(' ').join('-') }
                        }"
                            class="footer-link-product"
                          >{{ data.title }}</router-link>
                        </div>
                      </div>
                    </div>
                  </div>
                </v-flex>
              </v-layout>
            </v-card>
          </v-list>
        </v-menu>
      </v-toolbar-items>

      <div class="brand-logo text-md-center"></div>

      <!-- <v-spacer></v-spacer> -->
      <v-toolbar-items class="hidden-sm-and-down justify-end">
        <v-btn flat :ripple="false" :to="items[2].path" target="_self" class="pa-0 line">
          <span>{{ items[2].title }}</span>
        </v-btn>
      </v-toolbar-items>

      <v-toolbar-items class="hidden-sm-and-down justify-end">
        <v-btn flat :ripple="false" :to="items[3].path" target="_self" class="pa-0 line">
          <span>{{ items[3].title }}</span>
        </v-btn>
      </v-toolbar-items>

      <v-toolbar-items class="hidden-md-and-up justify-end">
        <v-btn flat :ripple="false" @click.stop="drawer1 = !drawer1">
          <img src="@/assets/icon/menu.svg" alt="Menu">
        </v-btn>

        <v-btn flat :ripple="false" @click.stop="drawer2 = !drawer2">
          <img src="@/assets/icon/contact.svg" alt="Contact">
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-navigation-drawer v-model="drawer1" absolute temporary right class="hidden-md-and-up">
      <v-list class="pa-3" dense>
        <v-list-tile v-for="item in items" :key="item.title" @click class="py-2 text-uppercase">
          <v-list-tile-content>
            <router-link :to="item.path" class="secondary--text body-2" @click.native="reloadPage(item.path)">{{ item.title }}</router-link>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-navigation-drawer v-model="drawer2" absolute temporary right class="hidden-md-and-up">
      <ContactInfo/>
    </v-navigation-drawer>
  </div>
</template>

<script>
import ContactInfo from "@/components/_ContactInfo"
import ProductLine from "../data/productline.json"

export default {
  name: "Toolbar",
  components: {
    ContactInfo
  },
  data: () => ({
    datas: ProductLine,
    isActive: true,
    items: [
      { title: "Overview", path: "/" },
      { title: "Products", path: "/products" },
      { title: "News & Event", path: "/news/2019" },
      { title: "About Us", path: "/about" }
    ],
    drawer1: null,
    drawer2: null,
  }),
  methods: {
    // handleScroll(event) {
    //   // Any code to be executed when the window is scrolled
    //   let header = document.querySelector(".main-toolbar");
    //   if (window.scrollY > 100) {
    //     header.classList.add("v-toolbar--scrolled");
    //   } else {
    //     header.classList.remove("v-toolbar--scrolled");
    //   }
    // },
    changeTheme(name) {
      if (name == "home") {
        this.isActive = true;
      } else {
        this.isActive = false;
      }
    },
    reloadPage(link) {
      this.$router.replace({
        path: link,
        query: {
          t: Date.now()
        }
      })
    }
  },
  created() {
    //window.addEventListener('scroll', this.handleScroll);
  },
  destroyed() {
    //window.removeEventListener('scroll', this.handleScroll);
  },
  mounted() {
    let name = this.$route.name;
    this.changeTheme(name);
  },
  watch: {
    $route(to, from) {
      this.changeTheme(this.$route.name);
    }
  },
  computed: {
    hasTabDatas() {
      return this.datas.filter(data => data.tabs && data.tabs.length > 2);
    },
    noTabDatas() {
      return this.datas.filter(data => !data.tabs || data.tabs.length < 2);
    }
  }
};
</script>
