<template>
  <section class="my-4">
    <v-container pa-0>
      <v-layout align-center column justify-center class="text-xs-center pa-4">
        <h3 class="display-1 font-weight-light">Brochure Download</h3>
        <span class="body-2 font-weight-light my-5">
          Youll find all the solutions you need in one place in our brochures.
          <br>Click the links below to download the brochure for the products youre interested in.
        </span>
      </v-layout>

      <v-layout row wrap>
        <v-flex xs12 sm4 md4 pa-4 v-for="(data, i) in hasTabDatas" :key="i">
          <v-card flat>
            <v-card-title class="warning--text subheading font-weight-medium">{{ data.title }}</v-card-title>
            <v-list>
              <v-list-tile v-for="(tab, j) in data.tabs" :key="j" @click.prevent="downloadFile(tab)">
                <v-list-tile-action>
                  <img src="@/assets/icon/icon-pdf.svg" alt="PDF">
                </v-list-tile-action>

                <v-list-tile-content>
                  <v-list-tile-title class="caption font-weight-light opacity-2">{{ tab }}</v-list-tile-title>
                </v-list-tile-content>

                <v-list-tile-action>
                  <img src="@/assets/icon/icon-download.svg" alt="PDF">
                </v-list-tile-action>
              </v-list-tile>
            </v-list>
          </v-card>
        </v-flex>

        <v-flex xs12 sm4 md4 pa-4>
          <v-card flat>
            <v-card-title class="warning--text subheading font-weight-medium">Others</v-card-title>
            <v-list>
              <div v-for="(data, i) in noTabDatas" :key="i">
                <v-list-tile v-for="(tab, j) in data.tabs" :key="j" @click.prevent="downloadFile(tab)">
                  <v-list-tile-action>
                    <img src="@/assets/icon/icon-pdf.svg" alt="PDF">
                  </v-list-tile-action>

                  <v-list-tile-content>
                    <v-list-tile-title class="caption font-weight-light opacity-2">{{ tab }}</v-list-tile-title>
                  </v-list-tile-content>

                  <v-list-tile-action>
                    <img src="@/assets/icon/icon-download.svg" alt="PDF">
                  </v-list-tile-action>
                </v-list-tile>
              </div>
            </v-list>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </section>
</template>

<script>
import axios from "axios";
import ProductLine from "../data/productline.json";
import Products from "../data/products.json";

export default {
  name: "Brochure",
  data() {
    return {
      datas: ProductLine
    };
  },
  computed: {
    hasTabDatas() {
      return this.datas.filter(data => data.tabs && data.tabs.length > 2);
    },
    noTabDatas() {
      return this.datas.filter(data => !data.tabs || data.tabs.length < 2 && data.title != 'Court');
    }
  },
  methods: {
    downloadFile(file) {
      axios({
        url: "../brochure/" + file + '.pdf',
        method: "GET",
        responseType: "blob"
      })
        .then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', file + '.pdf');
          document.body.appendChild(link);
          link.click();
        })
        .catch(() => console.log("error occured"));
    }
  }
};
</script>
