<template>
    <v-layout class="grey lighten-3" id="tab">
      <v-container py-3>
        <slot name="sublink"></slot>
      </v-container>
    </v-layout>
</template>

<script>
  export default {
    name: 'SubTab',
    data: () => ({
      yPosition: Number,
    }),
    methods: {
      handleScroll(event) {
        // Any code to be executed when the window is scrolled
        let tabbar = document.getElementById("tab");

        if (window.scrollY > this.yPosition) {
          tabbar.classList.add("tab-fixed");
        } else {
          tabbar.classList.remove("tab-fixed");
        }

      }
    },
    created() {
      window.addEventListener('scroll', this.handleScroll);
    },
    mounted() {
      let tabbar = document.getElementById("tab");
      this.yPosition = tabbar.offsetTop;
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll);
    },
  }
</script>
