<template>
  <section class="bg-color-gradient">
    <v-container>
      <v-layout row wrap>
        <v-flex sm12 mb-5>
          <v-layout align-center justify-space-between>
            <div class="headline">Careers</div>
            <div>
              <Jobs formid="Form1"/>
            </div>
          </v-layout>
          <p class="body-1 font-weight-light opacity-2 my-5">CSI is a leading application provider for government agencies and local,regional and statewide law enforcement agencies. Currently contracted by County Prosecutors Offices, the Supreme Court and State Police of New Jersey, the North Carolina
            Department of Justice in Raleigh, and others, CSI has established a reputation in developing and supporting web-based application solutions for various criminal justice, judiciary and regulatory agencies. CSI has also installed and supports
            applications in Hawaii, New Mexico, and Oklahoma.</p>
        </v-flex>

        <v-flex mt-3>
          <p class="headline">Benefits</p>
          <v-tabs grow v-model="active" color="transparent" dark slider-color="transparent" :height="iconSize+30" class="benifit-tab-btn">
            <v-tab v-for="i in items.length" :key="i" :ripple="false">
              <div>
                <img :src="require('@/assets/icon/benifits-0'+ i +'.svg')" :width="iconSize" :height="iconSize"/>
              </div>
            </v-tab>
            <v-tab-item v-for="(item, index) in items" :key="index" class="py-5">
              <v-card flat color="transparent text-sm-center">
                  <div class="title primary--text mb-4">{{ item.title }}</div>
                  <p class="body-2 font-weight-light">{{ item.content }}</p>
              </v-card>
            </v-tab-item>
          </v-tabs>

        </v-flex>
      </v-layout>
    </v-container>
  </section>
</template>

<script>
import Jobs from "../components/_Jobs"

  export default {
    name: 'Careers',
    components: {
      Jobs
    },
    data() {
      return {
        items: [{
            title: 'Cafeteria plan',
            content: 'Full-time employee w/90 days of service',
          },
          {
            title: 'Medical & dental insurance',
            content: 'Full-time employee w/90 days of service',
          },
          {
            title: 'Insurance',
            content: 'Worker\'s compensation coverage',
          },
          {
            title: 'Insurance',
            content: 'Company-paid state short-term disability insurance',
          },
          {
            title: 'Insurance',
            content: 'Company-paid life insurance',
          },
          {
            title: 'Insurance',
            content: 'Company-paid accidental death & dismemberment (AD&D) insurance',
          }
        ],
        active: null,
        polling: null,
      }
    },
    methods: {
      pollData() {
        this.polling = setInterval(() => {
          const active = parseInt(this.active)
          this.active = (active < 5 ? active + 1 : 0)
        }, 4000)
      },
    },
    computed: {
      iconSize() {
        switch (this.$vuetify.breakpoint.name) {
          case 'xs': return 44
          case 'sm': return 80
          case 'md': return 90
          case 'lg': return 104
          case 'xl': return 104
        }
      }
    },
    beforeDestroy() {
      clearInterval(this.active)
    },
    created() {
      this.pollData()
    }
  }
</script>
