          </v-container>
        </v-container>

        <section class="bg-gradient-top" :id="formid">
          <v-container>
            <v-layout>
              <v-flex sm8 offset-sm2>
                <v-container>
                  <v-layout row wrap>
                    <v-flex xs10 offset-xs2>
                      <h4 class="title white--text text-sm-center mb-5">Apply for this job</h4>
                    </v-flex>
                  </v-layout>

                  <v-form ref="form" @submit="validate" action="" method="post" enctype="text/plain" role="form" target="_top">
                    <v-layout row wrap>
                      <v-flex xs3 sm2 class="text-xs-right">
                        <div class="body-1 white--text mt-2 mr-3">Name</div>
                      </v-flex>
                      <v-flex xs9 sm10>
                        <v-text-field
                          class="round"
