                          flat
                          single-line
                          solo
                          small
                          background-color="grey lighten-3"
                          required
                        ></v-text-field>
                      </v-flex>

                      <v-flex xs3 sm2 class="text-xs-right">
                        <div class="body-1 white--text mt-2 mr-3">Email</div>
                      </v-flex>
                      <v-flex xs9 sm10>
                        <v-text-field
                          class="round"
                          v-model="email"
                          name="email"
