<script>
import Careers from "../data/careers.json";

export default {
  name: "Jobs",
  props: ['formid'],
  $_veeValidate: {
    validator: 'new'
  },
  data() {
    return {
      dialog: false,
      datas: Careers,
      valid: true,
      name: '',
      email: '',
      phone: '',
      message: '',
      rules: {
        required: v => !!v || 'Required',
        email: v => /.+@.+/.test(v) || "E-mail must be valid",
        phone: v => v.length >= 10 || "Phone must be valid"
      },
    };
  },
  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.$validator.validateAll();
        this.sendMail();
      } else {
        event.preventDefault();
      }
    },
    reset() {
      this.$refs.form.reset()
    },
    closeDialog() {
      this.dialog = false;
      this.reset();
    },
    sendMail() {
      let url = `mailto:mlin@csitech.com?subject=Apply for Job: Project Manager&body=${this.message}%0d%0a--------------------------------------------%0d%0a${this.name}%0d%0a Phone: ${this.phone}%0d%0a Email: ${this.email}`;
      window.location = url;
    }
  }
};
</script>

